import Settings from '../Settings';
const defaultChannels = [{
  number: 1,
  name: 'Metro News 1',
  description: 'New York Cable News Channel',
  entitled: true,
  program: {
    title: 'The Morning Show',
    description: "New York's best morning show",
    startTime: new Date(new Date() - 60 * 5 * 1000).toUTCString(),
    // started 5 minutes ago
    duration: 60 * 30,
    // 30 minutes
    ageRating: 0
  }
}, {
  number: 2,
  name: 'MTV',
  description: 'Music Television',
  entitled: true,
  program: {
    title: 'Beavis and Butthead',
    description: 'American adult animated sitcom created by Mike Judge',
    startTime: new Date(new Date() - 60 * 20 * 1000).toUTCString(),
    // started 20 minutes ago
    duration: 60 * 45,
    // 45 minutes
    ageRating: 18
  }
}, {
  number: 3,
  name: 'NBC',
  description: 'NBC TV Network',
  entitled: false,
  program: {
    title: 'The Tonight Show Starring Jimmy Fallon',
    description: 'Late-night talk show hosted by Jimmy Fallon on NBC',
    startTime: new Date(new Date() - 60 * 10 * 1000).toUTCString(),
    // started 10 minutes ago
    duration: 60 * 60,
    // 1 hour
    ageRating: 10
  }
}];
export const channels = () => Settings.get('platform', 'tv', defaultChannels);
export const randomChannel = () => channels()[~~(channels.length * Math.random())];